import {
  Modal,
  Container,
  Row,
  Tab,
  Tabs,
  Button,
  Form,
  Dropdown,
  DropdownButton,
} from "react-bootstrap";
import React from "react";
import Sha256 from "crypto-js/sha256";
import Hex from "crypto-js/enc-hex";
import { getProfile, getProfileLists } from "./storage";
import NFTCard from "./NFTCard";
import {
  updateProfile,
  updateCollection,
  queryCollectionByOwner,
  queryUnassignedNftsByOwner,
  setCollectionItem,
} from "./api";
import GetWalletModal from "./GetWalletModal";
import CustomDivider from "./CustomDivider";
import CollectionCard from "./CollectionCard";
import EditCollectionModal from "./EditCollectionModal";
import { useParams } from "react-router-dom";

function ProfileController() {
  const { address } = useParams();
  const [editShow, setEditShow] = React.useState(null);
  const [name, setName] = React.useState("");
  const nameRef = React.createRef();
  const [profile, setProfile] = React.useState(null);
  const [profileLists, setProfileLists] = React.useState(null);

  const [getWalletShow, setGetWalletShow] = React.useState(false);
  const [createCollectionShow, setCreateCollectionShow] = React.useState(false);

  const [collections, setCollections] = React.useState(null);

  React.useEffect(() => {
    if (address !== null) {
      document.title = `Profile | ${address}`;
      (async () => {
        const collections = await queryCollectionByOwner({ owner: address });
        if (collections !== null) {
          setCollections(collections.reverse());
        }

        const p = await getProfile(address);
        const pList = await getProfileLists(address);
        const unassignedNfts = await queryUnassignedNftsByOwner({
          owner: address,
        });

        setProfile(p);
        setProfileLists({
          createdNfts: pList.createdNfts.reverse(),
          unassignedNfts: unassignedNfts.reverse(),
          ownedNfts: pList.ownedNfts.reverse(),
          historicalNfts: pList.historicalNfts.reverse(),
        });

        if (p !== null) {
          if (p.name === null) {
            setName("");
          } else {
            setName(p.name);
            document.title = `Profile | ${p.name}`;
          }
        }
      })();
    }
  }, []);

  const doSubmitEdit = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();

    if (window.dingo === undefined) {
      setGetWalletShow(true);
      return;
    }

    const payload = {
      timestamp: Date.now(),
      owner: address,
      name: name === null || name === "" ? null : name,
    };
    const message = JSON.stringify(payload);

    const signResult = await window.dingo.requestSign(
      Hex.stringify(Sha256(message))
    );
    if (signResult.error !== undefined) {
      return;
    }
    payload.signature = signResult.result;

    const updateResult = await updateProfile(payload);
    if ("error" in updateResult) {
      return;
    }

    window.location.reload(false);
  };

  const doCreateCollectionSubmit = async (profile) => {
    // Create payload and signature message.
    const payload = {
      timestamp: Date.now(),
      owner: address,
      handle: profile.handle,
      name: profile.name,
      thumbnail: profile.thumbnail,
      description: profile.description,
    };
    const message = JSON.stringify(payload);

    // Sign.
    const signResult = await window.dingo.requestSign(
      Hex.stringify(Sha256(message))
    );
    if (signResult.error !== undefined) {
      return;
    }
    payload.signature = signResult.result;

    // Send.
    const updateResult = await updateCollection(payload);
    if ("error" in updateResult) {
      return;
    }

    window.location.reload(false);
  };

  const doMove = async (address, handle) => {
    // Create payload and signature message.
    const payload = {
      timestamp: Date.now(),
      address: address,
      handle: handle,
    };
    const message = JSON.stringify(payload);

    // Sign.
    const signResult = await window.dingo.requestSign(
      Hex.stringify(Sha256(message))
    );
    if (signResult.error !== undefined) {
      return;
    }
    payload.signature = signResult.result;

    // Send.
    const updateResult = await setCollectionItem(payload);
    if ("error" in updateResult) {
      return;
    }

    window.location.reload(false);
  };

  return (
    <div>
      <Container>
        <Row>
          <div className="align-middle" className="profile-header mt-4">
            <h3>
              {profile === null
                ? "-"
                : profile.name === "" || profile.name === null
                ? "Unnamed Profile"
                : profile.name}
            </h3>
            <h6>{address}</h6>
            <p>
              <span
                className="clickable"
                style={{ display: "inline" }}
                onClick={() => setEditShow(true)}
              >
                (Edit)
              </span>
            </p>
          </div>
        </Row>
        <CustomDivider />
        <Row>
          <h1 className="text-center">Profile Items</h1>
          <Tabs
            defaultActiveKey={"created"}
            className="justify-content-center mt-4"
            variant="pills"
          >
            <Tab
              eventKey="created"
              title="NFT creations"
              className="text-center"
            >
              <h3>Collections</h3>
              <p>
                <span
                  className="clickable"
                  style={{ display: "inline" }}
                  onClick={() => setCreateCollectionShow(true)}
                >
                  (Create new)
                </span>
              </p>
              <ul className="collection-cards-container">
                {collections !== null &&
                    collections.map((x) => <a key={x} href={`/collection/${x}`}><CollectionCard handle={x} /></a>)}
              </ul>
              {profileLists !== null && profileLists.unassignedNfts.length > 0 && (
                <div>
                  <h3 className="mt-5 mb-3">Unassigned</h3>
                  <ul className="cards-container">
                    {profileLists.unassignedNfts.map((x) => (
                      <div key={x}>
                        <a href={`/nft/${x}`}>
                          <NFTCard address={x} />
                        </a>
                        <DropdownButton
                          className="mt-3 px-4"
                          title="Move to collection"
                        >
                          {collections !== null &&
                            collections.map((y) => (
                              <Dropdown.Item
                                key={y}
                                onClick={() => doMove(x, y)}
                              >
                                {y}
                              </Dropdown.Item>
                            ))}
                        </DropdownButton>
                      </div>
                    ))}
                  </ul>
                </div>
              )}
            </Tab>
            <Tab eventKey="owned" title="Owned NFTs">
              {profileLists !== null && (
                <ul className="cards-container">
                  {profileLists.ownedNfts
                    .filter(
                      (x) => !profileLists.createdNfts.some((y) => y === x)
                    )
                    .map((x) => (
                      <a key={x} href={`/nft/${x}`}>
                        <NFTCard address={x} />
                      </a>
                    ))}
                </ul>
              )}
            </Tab>
            <Tab eventKey="previous" title="Previously owned">
              {profileLists !== null && (
                <ul className="cards-container">
                  {profileLists.historicalNfts
                    .filter((x) => profileLists.ownedNfts.every((y) => x !== y))
                    .filter(
                      (x) => !profileLists.createdNfts.some((y) => y === x)
                    )
                    .map((x) => (
                      <a key={x} href={`/nft/${x}`}>
                        <NFTCard address={x} />
                      </a>
                    ))}
                </ul>
              )}
            </Tab>
          </Tabs>
        </Row>
      </Container>

      <Modal
        size="lg"
        centered
        show={editShow}
        onHide={() => setEditShow(false)}
        onEntered={() => nameRef.current.focus()}
      >
        <Modal.Header closeButton>
          <Modal.Title>Edit profile</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={doSubmitEdit}>
            <Form.Group className="mb-3">
              <Form.Label>Profile name (leave blank if no change)</Form.Label>
              <Form.Control
                type="text"
                value={name}
                placeholder="Max 40 characters"
                onChange={(e) => setName(e.target.value)}
                isValid={name !== "" && name.length <= 40}
                isInvalid={name !== "" && name.length > 40}
                ref={nameRef}
              />
              {name.length > 40 && (
                <div>
                  <Form.Text className="input-error">Name too long</Form.Text>
                </div>
              )}
            </Form.Group>

            <div style={{ textAlign: "center" }}>
              <Button
                className="popup-button mb-3 mt-3 px-4"
                variant="primary"
                type="submit"
              >
                Verify and update profile
              </Button>
            </div>
          </Form>
        </Modal.Body>
      </Modal>

      <GetWalletModal
        show={getWalletShow}
        onHide={() => setGetWalletShow(false)}
      />

      <EditCollectionModal
        title="Create new collection"
        show={createCollectionShow}
        owner={address}
        onHide={() => setCreateCollectionShow(false)}
        onSubmit={doCreateCollectionSubmit}
      />
    </div>
  );
}

export default ProfileController;
