import React from "react";
import { Container } from "react-bootstrap";
import { getPreviewLink, getProfile } from "./storage";
import { getCollection } from "./api";

function CollectionCard(props) {
  const domRef = React.useRef();
  const [isVisible, setVisible] = React.useState(false);
  const [previewLink, setPreviewLink] = React.useState(null);
  const [name, setName] = React.useState(null);
  const [description, setDescription] = React.useState(null);
  const [owner, setOwner] = React.useState(null);

  React.useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          setVisible(true);
        }
      });
    });
    observer.observe(domRef.current);
    return () => {
      try {
        observer.unobserve(domRef.current);
      } catch {}
    };
  }, []);

  React.useEffect(() => {
    (async () => {
      if (isVisible) {
        const collection = await getCollection({ handle: props.handle });

        if (collection !== null) {
          setPreviewLink(await getPreviewLink(collection.thumbnail));
          setName(collection.name);
          setDescription(collection.description);
          const owner = await getProfile(collection.owner);
          if (owner !== null) {
            owner.address = collection.owner;
            setOwner(owner);
          }
        }
      }
    })();
  }, [props.address, isVisible]);

  return (
    <Container className="collection-card floating-borders" ref={domRef}>
      <div className="header-box d-flex flex-column">
        <img className="mx-auto mt-4 mb-4" src={previewLink} />
        <div className="mx-auto">{name}</div>
        <div
          className="mx-auto text-muted text-start mb-4 text-truncate"
          style={{ maxWidth: "15rem" }}
        >
          by{" "}
          {owner !== null && (
            <a href={`/profile/${owner.address}`} className="simple-link">
              {owner.name}
            </a>
          )}
        </div>
      </div>
      {/*
      <div className="card-sub-body text-muted d-flex flex-row justify-content-center mb-3 text-center">
        <span className="mx-1">
          <FontAwesomeIcon icon={faListOl} />
          <br />
          12399
        </span>
        <span className="mx-3">
          <FontAwesomeIcon icon={faCashRegister} />
          <br />
          999,999,999
        </span>
        <span className="mx-1">
          <FontAwesomeIcon icon={faExchangeAlt} />
          <br />
          5432
        </span>
      </div>
      */}
    </Container>
  );
}

export default CollectionCard;
