import { Container, Row } from "react-bootstrap";
import React from "react";
import { getCollectionItems, getCollection } from "./api";
import NFTCard from "./NFTCard";
import CustomDivider from "./CustomDivider";
import CollectionCard from "./CollectionCard";
import { useParams } from "react-router-dom";

function CollectionController() {
  const { handle } = useParams();
  const [collection, setCollection] = React.useState(null);
  const [items, setItems] = React.useState(null);

  React.useEffect(() => {
    (async () => {
      if (handle !== null) {
        setCollection(await getCollection({handle: handle}));
        const items = await getCollectionItems({ handle: handle });
        setItems(items);
      }
    })();
  }, []);

  return (
    <div>
      <Container>
        <Row>
          <div className="collection-cards-container mb-4">
            <CollectionCard handle={handle} />
          </div>
          {collection !== null &&
            collection.description !== null &&
            collection.description !== "" && (
              <div>
                <p className="text-center mb-4">
                  <i>{collection.description}</i>
                </p>
              </div>
            )}
        </Row>
        <CustomDivider />
        <Row>
          <h1 className="text-center">Collection Items</h1>
          <ul className="cards-container mt-4">
            {items !== null &&
              items.map((x) => (
                <a key={x} href={`/nft/${x}`}>
                  <NFTCard address={x} />
                </a>
              ))}
          </ul>
        </Row>
      </Container>
    </div>
  );
}

export default CollectionController;
